import React from 'react';
import { Link, withIntl } from '../i18n';
import PageWrapper from '../components/PageWrapper';
import { ArtistName, ContentTitle, Directory, PageTitle } from '../ui/utilities';
import { join } from '../helpers';

const Artists = ({ t, ...context }) => {
  const artistsList = t('artists:all', { returnObjects: true });
  let artists = Object.keys(artistsList);
  artists = artists.sort((a, b) => {
    const artistA = artistsList[a].lastname.toUpperCase();
    const artistB = artistsList[b].lastname.toUpperCase();
    return artistA > artistB ? 1 : artistB > artistA ? -1 : 0;
  });
  let letter = '';

  return (
    <PageWrapper small title={t('artists.title')} {...context}>
      <PageTitle>{t('artists.title')}</PageTitle>
      <ContentTitle className="t-h1" aria-hidden="true">{t('artists.title')}</ContentTitle>
      {artists.length && (
        <Directory>
          {artists.map(slug => {
            // If author only have 1 work redirect to it
            // const redirect = author.works.length > 1 ? `/${author.key}` : `/${author.key}/${author.works[0].key}`;
            const artist = artistsList[slug];
            const redirect = `/${slug}`;
            const comma = artist.name || artist.preposition ? ',' : '';
            const artistName = artist.preposition ? `<span>${join([artist.name, artist.preposition], ' ')}</span>` : artist.name ? `<span>${artist.name}</span>` : '';
            const artistFirstLetter = artist.lastname.charAt(0).toUpperCase();
            const isNewLetter = artistFirstLetter !== letter;
            letter = artistFirstLetter;

            if (!artist.available) {
              return;
            }

            const SEP = isNewLetter ? <li className='letter'>{letter}</li> : null;

            return (
              <React.Fragment key={`artist_${slug}`}>
                {SEP}
                <li>
                  <Link to={redirect}>
                    <ArtistName
                      as='h2'
                      dangerouslySetInnerHTML={{
                        __html: join([`<b>${artist.lastname}${comma}</b>`, artistName], ' ')
                      }}
                    />
                  </Link>
                </li>
              </React.Fragment>
            );
          })}
        </Directory>
      )}
    </PageWrapper>
  );
};

export default withIntl()(Artists);
